.container{
    background-color: #fdfdfd;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
   align-items: center;
}
.innerBox{
    background: linear-gradient(0deg, rgb(120, 34, 195) 0%, rgb(45, 229, 253) 100%);
    padding: 30px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    width: 450px;
}
.btn{
    width: 100%;
    padding: 20px;
    border-radius: 20px;
    text-align: center;
    border: none;
    margin-top: 10px;
    background-color: #0cc6f9;
    font-size: 25px;
    font-weight: bold;
}
.linkContainer{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.linkContainer span{
   
    padding: 10px 25px;
    border-radius: 10px;
    text-align: center;
    border: none;
    
    background-color: #0cc6f9;
    font-size: 15px;
    font-weight: bold;
}
.linkContainer span a{
    text-decoration: none;
}
.footer b{
    color: red;
    margin-top: 10px;
}